@import './partials/card/card.scss';
@import './partials/card2/card2.scss';
@import './partials/card3/card3.scss';
@import './partials/flex/flex.scss';

/*html * {
  outline: 1px solid red;
}
*/

.menu {
  width: 100%;
  display: -webkit-inline-flex;
  display: inline-flex; 
  justify-content: space-between;
  position: fixed;
  top: 0px;
  background-color: white;
  min-height: 180px;
}

.menu-logo {
  padding-top: 30px;
  -webkit-transition: width 2s, height 2s, background-color 2s, -webkit-transform 2s;
  transition: width 2s, height 2s, background-color 2s, transform 2s;
  transition-duration: 5s;
  width: 100%;
}

.menu-logo:hover {
  background-color: brown;
  width: 100%;
  -webkit-transform: ease(180deg);
  transform: ease(180deg);
  transition-duration: 5s;
  transition-delay: 1s;
  transition-property: inherit;
  transition-timing-function: ease-in-out;
}

.logo-desc {
  display: -webkit-inline-flex;
  display: inline-grid; 
  vertical-align: top;
  font-size: 40px;
}

.logo-desc>a {
  font-family: fantasy;
  font-size: 30pt;
  text-decoration-line: none;
  /*color: rgb(85, 26, 139);*/
  color: gray;
}

.logo-desc>a:hover {
  color: orange;
}

.menu-items {
  
  display: -webkit-inline-flex;
  display: inline-flex; 
  background-color: orange;
}

.menu-item {
  display: inline-grid;
  min-width: 150px;
  align-content: center;
  
}

.menu-item>a {
  text-align: center;
  font-family: fantasy;
  font-size: 20pt;
  text-decoration-line: none;
  /*color: rgb(85, 26, 139);*/
  color: white;
}

.menu-item>a:hover {
  color: gray;
}

.menu-item>img {
  display: block;
  margin: 0 auto; 
  width: 50px;
  height: 45px;
}

.menu-item>img:hover {
  width: 70px;
  height: 70px;
}

.img-background {
  background-image: url("/img/1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  /*width: 100%;*/
  height: 300px;
  padding-top: 130px;
  padding-left: 130px;
  background-attachment: fixed;
  margin-top: 180px;
}

.img-desc {
  font-family: fantasy;
  font-size: 40pt;
  width: 30%;
  min-width: 500px;
  
}

.img-desc-first {
  background-color: orange;
  color: white;
}

.img-desc-second {
  background-color: brown;
  color: white;
}

* {box-sizing:border-box}

.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

.mySlides {
  display: none;
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}


.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}